@import '~node_modules/modern-normalize/modern-normalize.css';
body {
  font-family: 'Roboto', sans-serif;
  font-style: 400;
  font-size: 14px;
  line-height: 1.14;
}
body.is-hidden {
  overflow: hidden;
}
.container {
  min-width: 320px;
  max-width: 480px;
  padding: 20px;
  margin: 0 auto;
  @media (min-width: 768px) {
    max-width: 768px;
    padding: 32px;
  }
  @media (min-width: 1280px) {
    max-width: 1280px;
  }
}
